/* Navbar */
.navbar {
    background: var(--navbar-bg);
    color: var(--navbar-text);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
}

.navbar-container {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}

.navbar-logo {
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--navbar-text);
    text-decoration: none;
}

/* Navigation Menu */
.nav-menu {
    display: flex;
    gap: 20px;
    align-items: center;
    transition: all 0.3s ease-in-out;
    margin-right: 60px; /* Ensures spacing before dark mode toggle */
}

/* Dark Mode Toggle */
.dark-mode-toggle {
    background: none;
    border: none;
    color: var(--navbar-text);
    font-size: 1rem;
    cursor: pointer;
    padding: 5px 15px;
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
}

/* Fix for Contact & Dark Mode Overlap */
.nav-menu li:last-child {
    margin-right: 80px; /* Adjust this spacing to separate Contact & Toggle */
}

/* Mobile Layout */
@media screen and (max-width: 768px) {
    .menu-toggle {
        display: block;
        font-size: 2rem;
        cursor: pointer;
        position: absolute;
        right: 80px;
        top: 50%;
        transform: translateY(-50%);
    }

    .nav-menu {
        display: none;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 100%;
        left: 0;
        background: var(--navbar-bg);
        text-align: center;
        padding: 10px;
    }

    .nav-menu.active {
        display: flex;
    }

    .dark-mode-toggle {
        position: relative;
        right: 0;
        top: 0;
        margin-left: 10px;
    }
}

/* Restore Desktop Layout */
@media screen and (min-width: 769px) {
    .nav-menu {
        display: flex;
    }

    .menu-toggle {
        display: none;
    }
}

/* Dark Mode Variables */
:root {
    --navbar-bg: #ffffff;
    --navbar-text: #000000;
    --accent-color: #007bff;
}

.dark-mode {
    --navbar-bg: #1e1e1e;
    --navbar-text: #ffffff;
    --accent-color: #4da6ff;
}

/* Apply Dark Mode to Entire App */
.app-container {
    background: var(--navbar-bg);
    color: var(--navbar-text);
    transition: 0.3s ease-in-out;
}
