.certifications-container {
  text-align: center;
  padding: 20px;
}

.certifications-title {
  font-size: 2rem;
  color: #007bff;
  margin-bottom: 10px;
}

.certifications-subtext {
  font-size: 1rem;
  color: #666;
  margin-bottom: 20px;
}

/* Grid Layout */
.certifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  justify-content: center;
  padding: 20px;
}

/* Individual Certificate Card */
.cert-card {
  background: #fff;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
}

.cert-card:hover {
  transform: scale(1.05);
}

.cert-thumbnail {
  width: 100%;
  height: 150px;
  object-fit: contain;
  border-radius: 8px;
}

.cert-title {
  font-size: 1rem;
  font-weight: bold;
  color: #007bff;
  margin-top: 8px;
}

/* Modal Styling */
.cert-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.cert-modal {
  background: white;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  max-width: 90%;
  max-height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cert-modal-img {
  max-width: 100%;
  max-height: 90vh;
  border-radius: 10px;
}

/* Close Button */
.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: red;
  color: white;
  border: none;
  font-size: 18px;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 5px;
}
