@use "@carbon/react";
@use "@carbon/react/scss/reset";
@use "@carbon/react/scss/theme";
@use "@carbon/react/scss/colors";

/* Default Light Mode */
body {
  font-family: "IBM Plex Sans", sans-serif;
  background-color: #ffffff;
  color: #161616;
  transition: background 0.3s ease, color 0.3s ease;
}

/* Dark Mode */
body.dark {
  background-color: #161616;
  color: #f4f4f4;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  flex: 1; /* Pushes content upwards to prevent footer overlap */
}


/* ================== Footer Styling ================== */
.footer {
  text-align: center;
  padding: 1rem;
  background-color: #0f62fe;
  color: white;
  position: relative; /* Changed from fixed to relative */
  width: 100%;
  margin-top: 40px; /* Adds space between content and footer */
}

.dark .footer {
  background-color: #1c1c1c;
  color: white;
}


/* ================== Page Content Styling ================== */
.container {
  max-width: 900px;
  margin: auto;
  padding: 2rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
  }

  .navbar ul {
    flex-direction: column;
  }

  .container {
    padding: 1rem;
  }
}

/* ================== Home Page Styling ================== */
.home {
  text-align: center;
  margin-top: 50px;
}

h1 {
  font-size: 2.5rem;
  font-weight: bold;
  color: #0f62fe;
}

h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 20px;
}

p {
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Skills & Achievements Sections */
.skills, .achievements {
  background: #f4f4f4;
  padding: 20px;
  border-radius: 8px;
  margin: 20px auto;
  max-width: 700px;
  text-align: left;
}

.skills h3, .achievements h3 {
  color: #0f62fe;
  font-size: 1.5rem;
}

.skills ul, .achievements ul {
  list-style-type: none;
  padding: 0;
}

.skills li, .achievements li {
  padding: 5px 0;
  font-size: 1.1rem;
}

/* Dark Mode Adjustments */
.dark .skills, .dark .achievements {
  background: #262626;
  color: white;
}

/* Achievements Icons */
.achievements li {
  padding: 8px 0;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
}

.achievements li::before {
  content: "✅";
  margin-right: 10px;
}

/* ================== Resume Download Button ================== */
.home-buttons {
  margin-top: 20px;
  display: flex;
  gap: 15px;
  justify-content: center;
}

.download-btn {
  background-color: #0f62fe !important;
  color: white !important;
  font-size: 1.1rem;
  padding: 10px 20px;
}

/* ================== Certification Carousel ================== */
.carousel-container {
  display: flex;
  overflow-x: auto;
  gap: 1rem;
  padding: 1rem;
}

.carousel-container img {
  width: 250px;
  height: auto;
  border-radius: 5px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

/* Responsive Design for Carousel */
.swiper-container {
  width: 100%;
  height: 250px;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  width: 200px;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}





.projects {
  text-align: center;
  margin-top: 50px;
}

.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
  align-items: stretch; /* Ensures all boxes are same height */
}


.project-card {
  background: #f4f4f4;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.project-card h3 {
  color: #0f62fe;
  font-size: 1.3rem;
  text-align: center;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}


.project-card:hover {
  transform: scale(1.02);
}

.dark .project-card {
  background: #262626;
  color: white;
}

.project-card h3 {
  color: #0f62fe;
}

.project-card p {
  font-size: 1rem;
  margin: 10px 0;
}

.project-links {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  gap: 10px;
}


.blog {
  text-align: center;
  margin-top: 50px;
}

.blog p {
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.6;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.blog-card {
  background: #f4f4f4;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  text-align: left;
}

.blog-card:hover {
  transform: scale(1.02);
}

.dark .blog-card {
  background: #262626;
  color: white;
}

.blog-card h3 {
  color: #0f62fe;
  font-size: 1.3rem;
  word-wrap: break-word;
  overflow-wrap: break-word;
  max-width: 100%;
  white-space: normal;
}

.blog-card p {
  font-size: 1rem;
  margin: 10px 0;
  color: #333;
}

.dark .blog-card p {
  color: #ddd;
}

.blog-card a {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

/* Blog Image Styling */
.blog-image {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}

/* Error Message */
.error-message {
  color: red;
  font-size: 1.2rem;
}



// Contact

.contact {
  text-align: center;
  margin-top: 50px;
}

.contact p {
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.6;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 500px;
  margin: 20px auto;
  padding: 20px;
  background: #f4f4f4;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.dark .contact-form {
  background: #262626;
  color: white;
}

.contact-form input,
.contact-form textarea {
  font-size: 1rem;
  padding: 10px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.contact-form button {
  width: 100%;
  font-size: 1.1rem;
}

.success-message {
  font-size: 1.2rem;
  color: green;
  margin-top: 20px;
}

/* Contact Info Section */
.contact-info {
  margin-top: 40px;
}

.contact-info h2 {
  font-size: 1.5rem;
  color: #0f62fe;
}

.contact-info ul {
  list-style: none;
  padding: 0;
}

.contact-info li {
  font-size: 1.2rem;
  margin: 10px 0;
}

.contact-info a {
  text-decoration: none;
  color: #0f62fe;
  font-weight: bold;
}

.contact-info a:hover {
  text-decoration: underline;
}


// Certificates
.certifications {
  text-align: center;
  margin-top: 50px;
}

.certifications p {
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Certificate Grid */
.cert-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-top: 20px;
  justify-content: center;
}

/* Certificate Card */
.cert-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #f4f4f4;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.3s ease;
}

.cert-card:hover {
  transform: scale(1.05);
}

.dark .cert-card {
  background: #262626;
  color: white;
}

/* Certificate Title */
.cert-card h3 {
  color: #0f62fe;
  font-size: 1.1rem;
  margin-top: 10px;
  text-align: center;
}

/* Carbon Modal Styling */
.bx--modal {
  max-width: 90vw;
}

.bx--modal-content {
  display: flex;
  justify-content: center;
  align-items: center;
}
